import jsPDF from "jspdf";
import { imageShowUrl1 } from "../../services/BaseUrl";

const DaybookBankPdf = (data) => {
  console.info("data", data);
  // let data = dataObj;
  var doc = new jsPDF({
    format: "a4",
    putOnlyUsedFonts: true,
  });
  var width = doc.internal.pageSize.getWidth();
  var height = doc.internal.pageSize.getHeight();

  // start of pdf title
  var firstPageHeight = 0;
  if (data?.ins_info?.insProfilePhoto) {
    doc.addImage(
      // `${imageShowUrl1}/4cb612dde72a9751194291d492003167`,
      `${imageShowUrl1}/${data?.ins_info?.insProfilePhoto}`,

      "JPEG",
      9,
      3,
      25,
      25
    );
  }
  // doc.addImage(data?.ins_info?.affiliatedImage, "JPEG", width - 34, 2.5, 25, 25);
  doc.setLineWidth(8);
  doc.setDrawColor(255, 255, 255);
  doc.circle(21.4, 15, 16);
  doc.setLineWidth(0);

  doc.setLineWidth(8);
  doc.setDrawColor(255, 255, 255);
  doc.circle(width - 21.4, 15, 16);
  doc.setLineWidth(0);

  firstPageHeight = data?.ins_info?.insAffiliated ? 12 : 10;

  const instituteName = doc.splitTextToSize(data?.ins_info?.insName ?? "", 146);
  doc.setFont("700");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(16);
  doc.text(107, firstPageHeight, instituteName, "center");
  if (instituteName?.length) {
    for (let i = 0; i < instituteName?.length; i++) {
      firstPageHeight += 6;
    }
  }
  if (data?.ins_info?.insAffiliated) {
    doc.setFontSize(10);
    doc.text(107, 5, data?.ins_info?.insAffiliated, "center");
  }

  doc.setFont("Arial", "normal", "400");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(10);
  doc.text(
    width / 2,
    firstPageHeight,
    data?.ins_info?.insAddress ?? "",
    "center"
  );

  doc.setFont("WorkSans");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(10);

  doc.text(
    width / 2,
    firstPageHeight + 4,
    `Mob No : ${data?.ins_info?.insPhoneNumber ?? ""} , Mail: ${
      data?.ins_info?.insEmail ?? ""
    }`,
    "center"
  );
  firstPageHeight += 20;

  doc.setTextColor(0, 0, 0);
  doc.setFontSize(13);

  let studentName = doc.splitTextToSize(
    `From: ${data?.day_range_from} To ${data.day_range_to}`,
    width / 2 - 9
  );
  doc.setFontSize(12);
  doc.text(9, firstPageHeight, studentName);
  firstPageHeight += 2;

  doc.setLineWidth(1);
  doc.setDrawColor(18, 18, 18);
  doc.line(0, firstPageHeight, width, firstPageHeight);

  firstPageHeight += 8;
  /// start feee receipt total values
  doc.setTextColor(0, 0, 0);
  doc.setFontSize(13);
  doc.text(
    9,
    firstPageHeight,
    `${data?.account_info?.finance_bank_name ?? ""}`
  );

  firstPageHeight += 15;
  doc.setTextColor(0, 0, 0);
  doc.setFontSize(11);
  let total_amount = 0;
  for (let i = 0; i < data?.results?.length; i++) {
    doc.setLineWidth(9);
    doc.setDrawColor("#d3d3d3");
    doc.line(0, firstPageHeight - 3, width, firstPageHeight - 3);
    doc.text(9, firstPageHeight - 1, `${data?.results[i]?.head_name ?? ""}`);
    doc.text(
      width - 17,
      firstPageHeight - 1,
      `${data?.results[i]?.head_amount ?? 0}`,
      "center"
    );
    total_amount += data?.results[i]?.head_amount;
    firstPageHeight += 8;
    if (firstPageHeight >= height) {
      doc.addPage({
        format: "a4",
        putOnlyUsedFonts: true,
      });
      firstPageHeight = 15;
    }
  }
  doc.setLineWidth(9);
  doc.setDrawColor("#d3d3d3");
  doc.line(0, firstPageHeight - 3, width, firstPageHeight - 3);
  doc.text(9, firstPageHeight - 1, `Total`);
  doc.text(width - 17, firstPageHeight - 1, `${total_amount}`, "center");
  firstPageHeight += 8;
  if (firstPageHeight >= height) {
    doc.addPage({
      format: "a4",
      putOnlyUsedFonts: true,
    });
    firstPageHeight = 15;
  }

  doc.setLineWidth(9);
  doc.setDrawColor("#d3d3d3");
  doc.line(0, firstPageHeight - 3, width, firstPageHeight - 3);
  doc.text(9, firstPageHeight - 1, `Total`);
  doc.text(width - 17, firstPageHeight - 1, `${total_amount}`, "center");
  firstPageHeight += 8;

  if (firstPageHeight >= height) {
    doc.addPage({
      format: "a4",
      putOnlyUsedFonts: true,
    });
    firstPageHeight = 15;
  }
  // window.open(
  //   doc.output("bloburl", {
  //     filename: `${studentInfo?.name}-admission-fee-receipt.pdf`,
  //   }),
  //   "_blank"
  // );
  return doc.output("bloburl", {
    filename: `bank-daybook.pdf`,
  });
};

export default DaybookBankPdf;
